    <!-- Âè≥‰æßÔºö‰∏™‰∫∫‰ø°ÊÅØÂíåÁ§æ‰∫§Âä®ÊÄÅ -->
    <main class="profile-main">
      <!-- ÈªòËÆ§È°µÈù¢ÔºàÂü∫Êú¨‰ø°ÊÅØ & Á§æ‰∫§Âä®ÊÄÅÔºâ -->
      <div v-show="currentSection === 'default'" id="default-content">
        <h2>üëã This is your profile!</h2>
        
        <!-- ‰∏™‰∫∫‰ø°ÊÅØ -->
        <div class="profile-info">
          <h3>üìÑ Basic Information</h3>
          <p>üéì Major: Computer Science & Math</p>
          <p>üìû Contact: {{ userStore.userProfile?.email }}</p>
        </div>

        <!-- ‰∏™‰∫∫Ê†áÁ≠æ -->
        <div class="profile-tags">
          <h3>üè∑Ô∏è Personal Tags</h3>
          <ul>
            <li>üíª Computer Science & Math</li>
            <li>üè† Dorm: McCarty Hall</li>
