          </ul>
        </div>

        <!-- ‰∏™‰∫∫Áà±Â•Ω -->
        <div class="profile-hobbies">
          <h3>üéØ Personal Hobbies</h3>
          <ul>
            <li>üéµ Music</li>
            <li>üèÄ Basketball</li>
