            :key="event.id"
            class="event-card-horizontal"
            @click="openDetail(event)"
            style="cursor: pointer;"
          >
            <h4>{{ event.title }}</h4>
            <div style="display: flex; justify-content: space-between; align-items: center;">
              <span>{{ formatDate(event.startime) }}</span>
